export default {
  name: "app",
  data(){
    return {
    }
  },
  created(){
    
  },
  mounted(){
     document.querySelector('#pageLoading').style.display = 'none';
  },
  watch: {
    $route(to,from){
      if(to.path === '/center' || to.path === '/index'){
        this.$store.dispatch('update_bottomShow',true)
      }else{
        this.$store.dispatch('update_bottomShow',false)
      }
    }
  }
};



